.EditContenu{
    width: 100%;
    position: fixed;
    top:0;
    left: 0;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    transform: scale(0);
    transition: all 0.3s ease;
    z-index:-10;
}
.Edit{
    display: grid;
    place-items: center;
    position: relative;
    top:19%;
    left:0%;
}
.show{
    transform: scale(1);
    z-index: 10;
}


